import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { useParams } from "react-router-dom";
import moment from 'moment';
import ListHeader from '../components/ListHeader';
import styled from 'styled-components';
import HeaderBar from '../components/HeaderBar';

function CarDetail() {
  const [detail, setDetail] = useState();
  const [attribute, setAttribute] = useState();

  let { id } = useParams();

  useEffect(() => {
    const fetchDatas = async () => {
      try {
        const response = await
          axios.get(`https://preonboarding.platdev.net/api/cars?id=${id}`,
          );

        const data = response.data.payload[id - 1]
        const attribute = data.attribute
      
        setDetail(data)
        setAttribute(attribute)
 
      } catch (error) {
        // console.log(error);
      }
    }
    fetchDatas()
  }, []);

  return (
    <>
      <HeaderBar isEdit={true} />
      <Container>
        <div className='image'>
          <img src={attribute?.imageUrl} />
        </div>
        <div className='text_title'>
          <p>{attribute?.brand}</p>
          <p>{attribute?.name}</p>
        </div>

        <div>
          <p> {detail?.amount.toLocaleString()}</p>
        </div>
        <ListHeader
          first={1}
        />
        <p> {attribute?.segment}</p>
        <p> {attribute?.fuelType}</p>
        <p>  {moment(detail?.startDate).format('MM DD(dd) ')}</p>
        <ListHeader
          second={2}
        />
        <p> {detail?.insurance[0].description}</p>
        <p> {detail?.insurance[1].description}</p>
        <ListHeader
          third={3} />
        <p>  {detail?.additionalProducts[0].amount.toLocaleString()}</p>
      </Container>
    </>
  )
}

const Container = styled.div`

img {
 height: 270px;
}
`;


export default CarDetail
